.miniLogo {
    width: 7%;
    height: auto;
    margin-right: 0.4rem;
    margin-left: 0px;
}

aside {
    grid-area: aside;
    height: 50vh;
    position: sticky;
    top: 7rem;
}

img {
    height:auto;
    width:50%;
}

@media screen and (min-width: 1150px) {
    .list {
        font-size: 1rem;
    }

    .miniLogo {
        width: 1.3rem;
    }
}

a {
    text-decoration: none;
    color:inherit;
}

svg {
    width: 4%;
    height: auto;
}

