.header {
    background-color: #330114;
    display: flex;
    width: 100%;
    height: 5rem;
    align-items: center;
    justify-content: space-around;
    position: fixed;
}

.header ul {
    list-style: none;
    display:flex;
}

.header li {
    color:white;
    margin-right: 2rem;
    /*font-size: 1.6vw;*/
    font-size: 1rem;
}

.name {
    margin-right: 3rem;
    color:white;
    font-weight: bold;
    /*font-size: 2vw;*/
    font-size: 1.3rem;
}

.header a {
    text-decoration: none;
    color: #cbbcbc;
}

.header a:hover,
.header a:active {
    color: white;
}

@media screen and (max-width: 500px) {
    .header li {
        font-size: 0.7rem;
    }

    .name {
        font-size: 0.9rem;
    }
}