@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  background-color: #fcf9f9;
  font-family: 'Poppins', sans-serif;
}

h1 {
  font-size: 1.7rem;
  color: #181617;
}

h2 {
  font-size: 1.5rem;
  color: #181617;
}

h3 {
  font-size: 1.3rem;
  color: #181617;
}

h4 {
  font-size: 0.8rem;
  color: #181617;
}