.blockInfo {
    margin-bottom: 1rem;
    margin-left: 5%;
    margin-right: 5%;

    background-color: white;
    padding: 1% 5% 1% 5%;
    border-radius: 8px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);

    font-size: 1.8vw;
}

.title {
    font-size: 2.3vw;
    font-weight: bold;
}

h3 {
    font-size: 2.3vw;
}

h4 {
    font-size: 1.6vw;
}



@media screen and (min-width: 790px) {
    .title {
        font-size: 1.3rem;
    }

    .blockInfo {
        font-size: 0.9rem;
    }

    h4 {
        font-size: .8rem;
    }

    h3 {
        font-size: 1.3rem;
    }
}

